  render() {
    const { finding, size } = this.props

    switch (finding.platform.toLowerCase()) {
      case 'aws':
        return this.renderIconAWS(size)
      case 'gcp':
        return this.renderIconGCP(size)
      case 'azure':
        return this.renderIconAzure(size)
      case 'k8s':
        return this.renderIconK8s(size)
      default:
        return this.renderIconGlobal(size)
    }
  }
